import React from "react";
import { Link } from "react-router-dom";
import PropsType from "prop-types";
import styles from "./Movie.module.css";

function Movie({ id, coverImg, title, year, summary, genres, movie_style }) {
  if (coverImg === "") {
    return null;
  }
  return (
    <div className={styles.movie} style={movie_style}>
      <img className={styles.movie__img} src={coverImg} alt={title} />
      <div>
        <h2 className={styles.movie__title}>
          <Link to={`/movie/${id}`}>
            {title.length > 50 ? `${title.slice(0, 50)}...` : title}
          </Link>
        </h2>
        <h3 className={styles.movie__year}>{year}</h3>
        <p>{summary.length > 235 ? `${summary.slice(0, 235)}...` : summary}</p>
        <ul className={styles.movie__genres}>
          {genres && genres.map((g) => <li key={g}>{g}</li>)}
        </ul>
      </div>
    </div>
  );
}

Movie.PropsType = {
  coverImg: PropsType.string.isRequired,
  title: PropsType.string.isRequired,
  summary: PropsType.string.isRequired,
  genres: PropsType.arrayOf(PropsType.string),
};

export default Movie;
