import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { Link } from "react-router-dom";
import { useRecoilState, useSetRecoilState } from "recoil";
import { focusNav, listPageReLoading } from "../atom/Atoms";
import Loding from "../component/Loading";
import Movie from "../component/Movie";
import styles from "./List.module.css";
/*상단바 메뉴 선택했을 때 상세 페이지 (페이지 밑에 1~10번까지 페이지 숫자 구현)*/

const listNums = [...Array(10)].map((_, i) => i + 1);

function List() {
  const { num, detail } = useParams(); /*페이지 넘버, 그에 맞는 상세페이지*/

  const [loading, setLoding] = useState(true);
  const [movie, setMovie] = useState([]);
  const [reloading, setReloading] = useRecoilState(listPageReLoading);
  const focusPage = useSetRecoilState(focusNav);

  const getMovies = async () => {
    const json = await (
      await fetch(
        `https://yts.mx/api/v2/list_movies.json?page=${num}&${detail}&sort_by=year`
      )
    ).json();
    setMovie(json.data.movies);
    setLoding(false);
  };
  useEffect(() => {
    setReloading(false);
    setLoding(true);
    focusPage(detail);

    getMovies();
  }, [reloading]);

  return (
    <div className={styles.container}>
      {loading ? (
        <Loding />
      ) : (
        <div className={styles.movies}>
          {movie.map((m) => (
            <Movie
              key={m.id}
              id={m.id}
              title={m.title}
              year={m.year}
              coverImg={m.medium_cover_image}
              summary={m.summary}
              genres={m.genres}
            />
          ))}
        </div>
      )}
      <ul className={styles.footer}>
        {loading
          ? null
          : listNums.map((listnum) => {
              return (
                <li>
                  <Link
                    to={`/page/${detail}/${listnum}`}
                    onClick={() => setReloading(true)}
                    className={listnum == num ? styles.focusing : null}
                  >
                    {listnum}
                  </Link>
                </li>
              );
            })}
      </ul>
    </div>
  );
}

export default List;
