import React from "react";
import { useState, useEffect } from "react";
import Loading from "./Loading";
import Movie from "./Movie";
import styles from "./Slide.module.css";
/*Home 화면에서 movie 좌우 슬라이드 화면 구현*/

function Slide({ ytsApi }) {
  const [loading, setLoding] = useState(true);
  const [movie, setMovie] = useState([]);
  const [trans, setTrans] = useState(0); /*현재 슬라이드의 값*/
  const onClickL = () => {
    if (trans >= 0) {
      /*왼쪽 마지막 슬라이드일시 return*/
      return;
    }
    setTrans((current) => current + 350);
  };
  const onClickR = () => {
    if (trans <= -2450) {
      /*오른쪽 마지막 슬라이드일시 return*/
      return;
    }
    setTrans((current) => current - 350);
  };
  const getMovies = async () => {
    const json = await (await fetch(ytsApi)).json();
    setMovie(json.data.movies);
    setLoding(false);
  };
  useEffect(() => {
    getMovies();
  }, []);
  return (
    <div className={styles.cotainer}>
      {loading ? (
        <Loading />
      ) : (
        <div className={styles.slide_show}>
          <div
            className={styles.slide}
            style={{ transform: `translateX(${trans}px)` }}
          >
            {movie.map((m) => (
              <Movie
                key={m.id}
                id={m.id}
                coverImg={m.medium_cover_image}
                title={m.title}
                year={m.year}
                summary={""}
                genres={m.genres}
                movie_style={{ minWidth: "350px", height: "300px" }}
              />
            ))}
          </div>
          {loading ? null : (
            <div>
              <button className={styles.left} onClick={onClickL}>
                <i class="fa-solid fa-chevron-left"></i>
              </button>
              <button className={styles.right} onClick={onClickR}>
                <i class="fa-solid fa-chevron-right"></i>
              </button>
            </div>
          )}
        </div>
      )}
    </div>
  );
}

export default Slide;
