import styles from "./Point.module.css";

function Point({
  bg_img,
  cover_img,
  url,
  title_long,
  rating,
  runtime,
  genres,
  download_count,
}) {
  return (
    <div>
      <img className={styles.bg} src={bg_img} alt={title_long} />
      <div className={styles.show}>
        <img className={styles.img} src={cover_img} alt={title_long} />
        <div className={styles.textbox}>
          <h1 className={styles.title}>
            <a href={url} target="_blank">
              {title_long}
            </a>
          </h1>
          {/*_blank : 링크된 문서를 새로운 윈도우나 탭에서 연결*/}
          <ul>
            <li>Rating {rating}</li>
            <li>Runtime {runtime}</li>
            <li>Download {download_count}</li>
            <li>
              Genres
              <ul>
                {genres.map((g) => (
                  <li>{g}</li>
                ))}
              </ul>
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
}

export default Point;
